import config from './config'
import init from './init'

init(config)

import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import NProgress from 'nprogress'

Vue.use(Router)
Vue.use(Resource)

// const apiURL = 'http://localhost:9292/api/v1'
// Vue.http.options.root = apiURL + '/proiel/20160607'
// Vue.config.debug = true

const apiURL = 'http://foni.uio.no:9292/api/v1'
Vue.http.options.root = apiURL + '/torot/20160616'

Vue.http.interceptors.push((request, next) => {
  NProgress.inc(0.2)

  next((response) => {
    NProgress.done()
  })
})

const { sidebar } = config

const router = new Router({
  saveScrollPosition: true,
  transitionOnLoad: true,
  linkActiveClass: 'is-active'
})

router.beforeEach(({ next }) => {
  NProgress.start()
  if (sidebar.isMobile && sidebar.opened) {
    sidebar.opened = false
  }
  next()
})

router.afterEach(() => {
  NProgress.done()
})

router.map({
  '/': {
    name: 'contents',
    component: require('./Contents.vue')
  },

  '/dictionary/:language': {
    name: 'dictionary',
    component: require('./Dictionary.vue')
  },

  '/dictionary/:language/lemma/:lemma/:part_of_speech': {
    name: 'lemma',
    component: require('./Lemma.vue'),
    subRoutes: {
      '/definition': {
        name: 'definition',
        component: require('./Lemma/Definition.vue')
      },

      '/tokens': {
        name: 'lemma-tokens',
        component: require('./Lemma/Tokens.vue')
      },

      '/paradigm': {
        name: 'paradigm',
        component: require('./Lemma/Paradigm.vue')
      },

      '/chronology': {
        name: 'chronology',
        component: require('./Lemma/Chronology.vue')
      },

      '/valency': {
        name: 'valency',
        component: require('./Lemma/Valency.vue')
      }
    }
  },

  '/sentence/:sentence': {
    name: 'sentence',
    component: require('./Sentence.vue')
  },

  '/source/:source': {
    name: 'source',
    component: require('./Source.vue')
  },

  '/tokens': {
    name: 'tokens',
    component: require('./Tokens.vue')
  },

  '/query': {
    name: 'query',
    component: require('./Query.vue')
  },

  '/search': {
    name: 'search',
    component: require('./Search.vue')
  }
})

router.redirect({
  '*': '/',
  '/dictionary/:language/lemma/:lemma/:part_of_speech': '/dictionary/:language/lemma/:lemma/:part_of_speech/definition'
})

import App from './App'

router.start(App, 'app')



