<template>
  <div>
    <section>
      <table class="table">
        <tbody>
          <tr v-for="token in tokens">
            <td><a v-link="{ name: 'sentence', params: { sentence: token.sentence }}">{{ token.citation }}</a></td>
            <td class="has-text-right" width="30%" :lang="language">{{ token.abbrev_text_before }}</td>
            <td class="highlight has-text-centered" :lang="language">{{ token.form }}</td>
            <td class="has-text-left" width="30%" :lang="language">{{ token.abbrev_text_after }}</td>
          </tr>
        </tbody>
      </table>
    </section>

    <div v-if="total > 1">
      <pagination :per-page="20" :on-change="onChange" :total="total"></pagination>
    </div>
  </div>
</template>

<script>
import Pagination from './Pagination'

export default {
  components: {
    Pagination
  },

  props: {
    language: null,
    tokens: Array,
    total: Number,
    onChange: Function
  }
}
</script>



