<template>
  <div class="container">
    <div class="notification is-danger">
      This function has not been implemented yet. This page is only a placeholder.
    </div>

    <div>
      <p>{{ sentence.citation }}</p>

      <p>{{ sentence.text }}</p>
    </div>

    <div>
      <span v-for="token in sentence.tokens">
        {{ token.form }}
        {{ token.part_of_speech }}
        {{ token.morphology }}
        {{ token.lemma }}
      </span>
    </div>

    <div class="has-text-centered">
      <svg id="svg-canvas" width="800" height="600">
        <g/>
      </svg>
    </div>
  </div>
</template>

<style>
svg {
  border: 1px solid #999;
  overflow: hidden;
}

text {
  font-weight: 300;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serf;
  font-size: 14px;
}

.box {
/*  fill: #f00; */
}

.node rect {
  stroke: #999;
  stroke-width: 1px;
  fill: #fff;
}

/*.edgeLabel rect {
  fill: #fff;
}*/

.edgePath path {
  stroke: #333;
  stroke-width: 1.5px;
  fill: none;
}
</style>

<script>
import api from './api'

/* eslint-disable */
function makeGraph(data) {
  var g = new dagreD3.graphlib.Graph({
    multigraph: true,
    compound: true,
  });

  g.setGraph({
    nodesep: 10,
    ranksep: 40,
    rankdir: "TB",
    marginx: 20,
    marginy: 20,
    ranker: "tight-tree"
  });

  g.setNode('ROOT', {  });

  for (var i in data.tokens) {
    var t = data.tokens[i];

    if (t.empty_token_sort === null) {
      g.setNode(t.id, {
        label: t.form,
        shape: 'rect'
      });
    } else {
      g.setNode(t.id, {
        label: t.empty_token_sort,
        shape: 'diamond',
        style: 'fill: #fff; stroke: #999; stroke-width: 1px',
      });
    }

    g.setEdge(t.head_id || 'ROOT', t.id, {
      label: t.relation.toUpperCase(),
      style: 'stroke: orange',
      lineInterpolate: 'basis',
    });
  }

  for (var i in data.tokens) {
    var t = data.tokens[i];
    for (var j in t.slashes) {
      var s = t.slashes[j];
      var relation = s[0].toUpperCase();
      var target = s[1];

      g.setEdge(t.id, target, {
        label: relation,
        weight: 0,
        style: 'stroke: blue; stroke-dasharray: 5, 5;',
        lineInterpolate: 'basis',
      });
    }
  }

  return g
}
/* eslint-enable */

export default {
  data () {
    return {
      sentence: {}
    }
  },

  ready() {
    let svg = d3.select("svg")
    let inner = d3.select("svg g")
    let zoom = d3.behavior.zoom().on("zoom", function() {
      inner.attr("transform", "translate(" + d3.event.translate + ")" + "scale(" + d3.event.scale + ")")
    })
    svg.call(zoom)
  },

  route: {
    data({ to, abort }) {
      let sentenceParams = {
        sentence: to.params.sentence
      }

      return api.getSentence(sentenceParams).then((data) => {
        return {
          sentence: data
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  },

  watch: {
    sentence(val) {
      this.$nextTick(() => {
        this.drawGraph()
      })
    }
  },

  methods: {
    drawGraph() {
      let g = makeGraph(this.sentence)

      g.graph().transition = function(selection) {
        return selection.transition().duration(500)
      }

      /* eslint-disable */
      let render = new dagreD3.render()
      /* eslint-enable */

      d3.select("svg g").call(render, g)
    }
  }
}
</script>



