<template>
  <div>
    <h1 class="title">Contents</h1>
    <h2 class="subtitle">Texts</h1>

    <table class="table">
      <tbody>
        <tr v-for="source in sources">
          <td><a v-link="{ name: 'source', params: { source: source.id }}">{{ source.title }}</a></td>
        </tr>
      </tbody>
    </table>

    <h2 class="subtitle">Dictionaries</h1>

    <table class="table">
      <tbody>
        <tr v-for="dictionary in dictionaries">
          <td><a v-link="{ name: 'dictionary', params: { language: dictionary.id, page: 1 }}">{{ dictionary.name }}</a></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import api from './api'

export default {
  data() {
    return {
      dictionaries: [],
      sources: []
    }
  },

  route: {
    data({ to, abort }) {
      return Promise.all([
        api.getDictionaries(),
        api.getSources()
      ]).then(data => {
        return {
          dictionaries: data[0],
          sources: data[1]
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  }
}
</script>



