// Based on https://github.com/fundon/vue-admin/blob/master/src/components/pages/Components/Message.vue
import Vue from 'vue'
import Message from 'vue-bulma-message'

const MessageComponent = Vue.extend(Message)

const openMessage = (propsData = {
  title: '',
  message: '',
  type: '',
  direction: '',
  duration: 1500,
  container: '.messages'
}) => {
  return new MessageComponent({
    el: document.createElement('div'),
    propsData
  })
}

export const openErrorMessage = msg => openMessage({
  message: msg,
  type: 'danger',
  duration: 0,
  showCloseButton: true
})

export const handleAPIError = error => {
  console.warn(error)

  if (error.status === 404) {
    openErrorMessage("Oops, we can't find that! Did you follow an old link?")
  } else {
    openErrorMessage("Oops, something went wrong! We may have some problems with out server...")
  }
}



