import Vue from 'vue'
import Vuex from 'vuex'

import config from '../config'

const { menu, sidebar } = config

Vue.use(Vuex)

const state = {
  menu,
  sidebar
}

const mutations = {
  CHANGE_TREEBANK(state, id) {
    console.log("Mutating")
  }
}

export default new Vuex.Store({
  state,
  mutations
})



