<template>
  <div>
    <h1 class="title">Dictionary</h1>
    <h2 class="subtitle">{{ language | humanizeLanguage }}</h2>

    <nav class="level">
      <div class="level-left">
      </div>
      <div class="level-right">
        <multiselect
          placeholder="Form"
          :selected="formFilter"
          @update='onFormFilterChange'
          @search-change='onFormFilterFind'
          :options="formOptions"
          :multiple="false"
          :searchable="searchable"
          :allow-empty="true"
          :show-labels="false">
        </multiselect>

        <multiselect
          placeholder="Part of speech"
          :selected="partOfSpeechFilter" @update='onPartOfSpeechFilterChange' :options="partOfSpeechOptions" :multiple="false" :searchable="false"
          :allow-empty="true"
          key="label" label="label" :show-labels="false" :custom-label="partOfSpeechLabel"></multiselect>
      </div>
    </nav>

    <table class="table">
      <tbody>
        <tr v-for="lemma in dictionary">
          <td><a v-link="{ name: 'lemma', params: { language: language, lemma: lemma.lemma, part_of_speech: lemma.part_of_speech }}"><span :lang="language">{{ lemma.lemma }}</span> ({{ lemma.part_of_speech | humanizePartOfSpeech }})</a> ‘{{ lemma.glosses.eng }}’</td>
        </tr>
      </tbody>
    </table>

    <pagination :per-page="20" :on-change="changePage" :total="total" :current-page="page"></pagination>
  </div>
</template>

<script>
import Pagination from './ui/Pagination'
import Multiselect from 'vue-multiselect'

import api from './api'
import filterMixin from './filters'

export default {
  name: 'DictionaryView',

  mixins: [filterMixin],

  components: {
    Pagination,
    Multiselect
  },

  data() {
    return {
      schema: {},
      dictionaries: [],

      page: 1,
      total: 0,
      dictionary: [],

      formOptions: []
    }
  },

  computed: {
    formFilter() {
      return this.$route.query.form
    },

    partOfSpeechFilter() {
      const tag = this.$route.query.part_of_speech

      return {
        tag: tag,
        label: this.schema.part_of_speech ? this.schema.part_of_speech[tag] : ''
      }
    },

    partOfSpeechOptions() {
      let k = []
      for (let tag in this.schema.part_of_speech) {
        k.push({
          tag: tag,
          label: this.schema.part_of_speech[tag]
        })
      }
      return k
    },

    language() {
      return this.$route.params.language
    }
  },

  route: {
    data({ to, abort }) {
      this.page = +to.query.page || 1

      let dictionaryParams = {
        language: to.params.language,
        part_of_speech: to.query.part_of_speech,
        lemma: to.query.form === '' ? null : to.query.form,

        page: this.page
      }

      return Promise.all([
        api.getDictionary(dictionaryParams),
        api.getSchema(),
        api.getDictionaries()
      ]).then(function(data) {
        return {
          total: data[0].total,
          dictionary: data[0].data,
          schema: data[1],
          dictionaries: data[2]
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  },

  methods: {
    onFormFilterChange(n) {
      let newQuery = this.$route.query
      newQuery.form = n
      newQuery.page = 1

      this.$router.go({
        name: 'dictionary',
        params: this.$route.params,
        query: newQuery
      })
    },

    onFormFilterFind(n) {
      this.formOptions = [n]
    },

    // FIXME: remove or use!
    partOfSpeechLabel(n) {
      return n ? this.humanizePartOfSpeech(n.tag) : ''
    },

    onPartOfSpeechFilterChange(n) {
      let newQuery = this.$route.query
      newQuery.part_of_speech = n ? n.tag : null
      newQuery.page = 1

      this.$router.go({
        name: 'dictionary',
        params: this.$route.params,
        query: newQuery
      })
    },

    changePage(page) {
      let newQuery = this.$route.query
      newQuery.page = +page || 1

      this.$router.go({
        name: 'dictionary',
        params: this.$route.params,
        query: newQuery
      })
    }
  }
}
</script>



