<template>
  <div>
    <concordance :tokens="tokens" :total="total" :on-change="changePage" :language="language"></concordance>
  </div>
</template>

<script>
import Concordance from './ui/Concordance'

import api from './api'

export default {
  components: {
    Concordance
  },

  data() {
    return {
      page: 1,
      total: 0,
      tokens: []
    }
  },

  computed: {
    language() {
      return this.$route.query.language
    }
  },

  route: {
    data({ to, abort }) {
      this.page = +to.query.page || 1

      let tokensParams = {
        frame_id: to.query.frame_id,
        source: to.query.source,
        language: to.query.language,
        form: to.query.form,
        lemma: to.query.lemma,
        morphology: to.query.morphology,
        part_of_speech: to.query.part_of_speech,
        relation: to.query.relation,
        information_status: to.query.information_status,
        person: to.query.person,
        number: to.query.number,
        tense: to.query.tense,
        mood: to.query.mood,
        voice: to.query.voice,
        gender: to.query.gender,
        case: to.query.case,
        degree: to.query.degree,
        strength: to.query.strength,
        inflection: to.query.inflection,

        page: this.page
      }

      return api.getTokens(tokensParams).then((data) => {
        return {
          tokens: data.data,
          total: data.total
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  },

  methods: {
    changePage(page) {
      let newQuery = this.$route.query
      newQuery.page = +page || 1

      this.$router.go({
        name: 'tokens',
        query: newQuery
      })
    }
  }
}
</script>



