<template>
  <div>
    <h3 class="subtitle">Valency</h3>

    <section>
      <table class="table">
        <thead>
          <tr>
            <th>Arguments</th>
            <th>Non-reflexive</th>
            <th>Reflexive</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="frame in valency">
            <td v-if="frame.arguments.length > 0">
              <span v-for="argument in frame.arguments">
                {{ argument.relation | uppercase }}
                {{{ featuresAsString(argument) }}}
              </span>
            </td>
            <td v-else>
              (none)
            </td>
            <td v-if="frame.partitions.a">
              <a v-link="{ name: 'tokens',  query: { frame_id: frame.partitions.a.frame_id }}">{{ frame.partitions.a.n }}</a>
            </td>
            <td v-else>
            </td>
            <td v-if="frame.partitions.r">
              <a v-link="{ name: 'tokens',  query: { frame_id: frame.partitions.r.frame_id }}">{{ frame.partitions.r.n }}</a>
            </td>
            <td v-else>
            </td>
          </tr>
        </tbody>
      </table>
    </section>
  </div>
</template>

<script>
import filterMixin from '../filters'

export default {
  name: 'LemmaValencyView',

  mixins: [filterMixin],

  computed: {
    valency() {
      return this.$parent.lemma.valency || []
    },

    schema() {
      return this.$parent.schema || {}
    }
  },

  methods: {
    featuresAsString(argument) {
      let s = ''

      if (argument.lemma) {
        s += this.humanizePartOfSpeech(argument.part_of_speech)
        s += ' <em>'
        s += argument.lemma
        s += '</em>'

        if (argument.case || argument.mood) {
          if (argument.part_of_speech === 'G-' || argument.part_of_speech === 'R-') {
            s += ' +'
          } else {
            s += ','
          }

          if (argument.case) {
            s += ' '
            s += this.humanizeCase(argument.case)
          }

          if (argument.mood) {
            s += ' '
            s += this.humanizeMood(argument.mood)
          }
        }
      } else {
        if (argument.case) {
          s += this.humanizeCase(argument.case)
        }

        if (argument.mood) {
          if (argument.case)
            s += ' '
          s += this.humanizeMood(argument.mood)
        }
      }

      return s === '' ? '' : '(' + s + ')'
    }
  }
}
</script>



