import Vue from 'vue'
import { handleAPIError } from './errors'

const cache = Object.create(null)

// const schema = Vue.http.resource('schema')
// const sources = Vue.http.resource('sources')
// const dictionaries= Vue.http.resource('dictionaries')
// const dictionary = Vue.http.resource('dictionaries/{language}/lemmas{?part_of_speech,page}')
// const lemma = Vue.http.resource('dictionaries/{language}/lemmas/{lemma}')
// const tokens = Vue.http.resource('tokens{?ids,source,language,form,lemma,morphology,part_of_speech,relation,information_status,person,number,tense,mood,voice,gender,case,degree,strength,inflection,page}')
// const sentence = Vue.http.resource('sentences/{sentence}')

export default {
  handleAPIError,

  get(path, params) {
    return Vue.http.get(path, { params: params }).then((response) => { return response.json() })
  },

  getCached(key, path, params) {
    if (cache[key]) {
      return new Promise((resolve, reject) => { resolve(cache[key]) })
    } else {
      return this.get(path, params).then((data) => {
        cache[key] = data
        return data
      })
    }
  },

  getSchema() {
    return this.getCached('schema', 'schema', {})
  },

  getSources() {
    return this.getCached('sources', 'sources', {})
  },

  getDictionaries() {
    return this.getCached('dictionaries', 'dictionaries', {})
  },

  getDictionary(params) {
    return this.get('dictionaries/{language}/lemmas{?form,part_of_speech,page}', params)
  },

  getLemma(params) {
    return this.get('dictionaries/{language}/lemmas/{lemma}/{part_of_speech}', params)
  },

  getTokens(params) {
    return this.get('tokens{?frame_id,source,language,form,lemma,morphology,part_of_speech,relation,information_status,person,number,tense,mood,voice,gender,case,degree,strength,inflection,page}', params)
  },

  getSentence(params) {
    return this.get('sentences/{sentence}', params)
  }
}



