<template>
  <div class="tile is-ancestor">
    <div class="tile is-parent">
      <article class="tile is-child">
        <h3 class="subtitle">Chronology by text</h3>
        <timeline :events="timelineText"></timeline>
      </article>
    </div>

    <div class="tile is-parent">
      <article class="tile is-child">
        <h3 class="subtitle">Chronology by manuscript</h3>
        <timeline :events="timelineMs"></timeline>
      </article>
    </div>
  </div>
</template>

<script>
import Timeline from '../ui/Timeline'

export default {
  name: 'LemmaChronologyView',

  components: {
    Timeline
  },

  computed: {
    timelines() {
      return this.$parent.lemma.distribution || []
    },

    timelineText() {
      return this.timelines.map(d => {
        let year = d.chronology.text
        return {
          label: year + ': ' + d.id + ' (' + d.n + ')',
          date: year
        }
      })
    },

    timelineMs() {
      return this.timelines.map(d => {
        let year = d.chronology.ms
        return {
          label: year + ': ' + d.id + ' (' + d.n + ')',
          date: year
        }
      })
    }
  }
}
</script>



