<template>
  <div>
    <h3 class="subtitle">Concordance</h3>

    <concordance :tokens="tokens" :total="total" :on-change="changePage" :language="language"></concordance>
  </div>
</template>

<script>
import api from '../api'
import Concordance from '../ui/Concordance'

export default {
  components: {
    Concordance
  },

  data() {
    return {
      page: 1,
      total: 0,
      tokens: []
    }
  },

  computed: {
    language() {
      return this.$route.params.language
    }
  },

  route: {
    data({ to, abort }) {
      this.page = +to.query.page || 1

      let tokensParams = {
        language: to.params.language,
        lemma: to.params.lemma,
        part_of_speech: to.params.part_of_speech,

        page: this.page
      }

      return api.getTokens(tokensParams).then((data) => {
        return {
          tokens: data.data,
          total: data.total
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  },

  methods: {
    changePage(page) {
      let newQuery = this.$route.query
      newQuery.page = +page || 1

      this.$router.go({
        name: 'lemma-tokens',
        query: newQuery
      })
    }
  }
}
</script>



