export default {
  methods: {
    humanizeFeature(feature, value) {
      if (this.schema && feature && this.schema[feature] && value)
        return this.schema[feature][value]
      else
        return ''
    },

    humanizePartOfSpeech(value) {
      return this.humanizeFeature('part_of_speech', value)
    },

    humanizeMood(value) {
      return this.humanizeFeature('mood', value)
    },

    humanizeCase(value) {
      return this.humanizeFeature('case', value)
    },

    humanizeLanguage(value) {
      if (this.dictionaries && value) {
        for (let i in this.dictionaries) {
          var d = this.dictionaries[i]
          if (d.id === value)
            return d.name
        }
        return ''
      } else
        return ''
    }
  },

  filters: {
    humanizePartOfSpeech(value) {
      return this.humanizeFeature('part_of_speech', value)
    },

    humanizeMood(value) {
      return this.humanizeFeature('mood', value)
    },

    humanizeCase(value) {
      return this.humanizeFeature('case', value)
    },

    humanizeLanguage(value) {
      if (this.dictionaries && value) {
        for (let i in this.dictionaries) {
          var d = this.dictionaries[i]
          if (d.id === value)
            return d.name
        }
        return ''
      } else
        return ''
    }
  }
}



