<template>
  <div class="ui segment">
    <h2 class="ui dividing header">Search</h2>

    <form class="ui form" v-on:submit.prevent="runQuery">
      <div class="field">
        <label :for="query-text">Query expression</label>
        <input type="text" id="query-text" v-model="query"></input>
      </div>
      <input type="submit" value="Run query">
    </form>

    <ul>
      <li v-for="result in results">
        <a href="http://foni.uio.no:3000/sentences/{{result.sentence}}">{{ result | extractMatches }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
const queryURL = 'http://foni.uio.no:5000/'

export default {
  name: 'QueryRunner',

  data () {
    return {
      query: "#np:[word=\"Atticum\"]",
      results: []
    }
  },

  filters: {
    extractMatches (v) {
      var matches = ""

      for (var k in v.matches) {
        if (v.matches.hasOwnProperty(k)) {
          if (matches !== '') {
            matches += ', '
          }
          matches += k + '=' + v.matches[k]
        }
      }

      return matches
    }
  },

  methods: {
    runQuery () {
      var xhr = new XMLHttpRequest()
      var self = this
      xhr.open('POST', queryURL)
      xhr.onload = function () {
        self.results = JSON.parse(xhr.responseText)
      }
      xhr.send(this.query)
    }
  }
}

//s.addExampleQuery('#np:[word="Atticum"]', '#np is the word form <i>Atticum</i>');
//s.addExampleQuery('#c:[pos="G\-" & word="ut"] & #v:[lemma="volo#1"] & #c >pred #v', '#c is the subjunction <i>ut</i> and it dominates #v, which has the lemma <i>volo#1</i>, as a <tt>pred</tt>');
//s.addExampleQuery('#c:[word="ut" & pos="G\-"] & #c >* #n & #n . #c & #n:[word!=""]', '#n is a dependent (at any depth) of the subjunction <i>ut</i> and #n precedes the subjunction');
//s.addExampleQuery('#s:[lemma="scribo"] & #x & #s >~ #x & #x >xobj #s', '#s has the lemma <i>scribo</i>, has an outgoing secondary edge (a slash) and the relation <tt>xobj</tt>');
//s.addExampleQuery('#s:[lemma="scribo"] & #x & #s >~ #x & #x !>xobj #s', '#s has the lemma <i>scribo</i>, has an outgoing secondary edge (a slash) but does not have the relation <tt>xobj</tt>');
//s.addExampleQuery('discontinuous(#c) & #c:[word="ut" & pos="G\-"]', '#c is the subjunction <em>ut</em> and its dependents are discontinuous');
</script>



