<template>
  <div>
    <h3 class="subtitle">Translations</h3>
    <div class="content" :lang="$key" v-for="gloss in glosses">
      {{ gloss }}
    </div>

    <h3 class="subtitle">Total number of occurrences</h3>
    <div>
      {{ frequency }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'LemmaDefinitonView',

  computed: {
    lemma() {
      return this.$parent.lemma || {}
    },

    glosses() {
      return this.lemma.glosses || {}
    },

    distribution() {
      return this.lemma.distribution || []
    },

    frequency() {
      return this.distribution.map(e => e.n).reduce((a, e) => a + e, 0)
    }
  }
}
</script>



